.section {
   margin: 10px 0;
   background: hsl(0, 0%, 100%);
}

.section__title {
   padding: 20px;
   margin: 0;
}

.section__header {
   display: grid;
   grid-template-columns: repeat(2, 1fr);
   align-items: center;
   border-bottom: 1px solid hsl(0, 0%, 87%);
}

.section__body {
   padding: 20px;
}